import { Box } from '@mui/material';
import React from 'react';

const CenteredContent = () => {










  return (
    <Box sx={{    display: {
          xs: "block",
      sm: "block",
      md: "flex",
      lg:"flex",
      xl: "flex",
        
        
        
    }, justifyContent:"space-between", alignItems: 'center',height:"auto",flexDirection:"initial",alignContent:"center"}}>
      <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center',height:"100%", }}>



        <img src="./images/send-to-cart.png" alt="mage" />
        <h1 style={{ textAlign: 'center',color:"#E04C55" ,fontSize:"2rem" }}>J'authentifie</h1> 
        <p  style={{ textAlign: 'center', fontWeight:"bolder", color:"gray",fontSize:"1.5rem" }}>Ma recharge Neosurf(Simple et rapide)</p>
      </div> 
     
      <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center',margin:'5px', height:"100%",}}>



        <img src="./images/carte-paie.png" alt="mage" />
        <h1 style={{ textAlign: 'center',color:"#E04C55" ,fontSize:"2rem" }}>Je reçois</h1>
        <p style={{ textAlign: 'center', fontWeight:"bolder", color:"gray",fontSize:"1.5rem" }}>Ma confirmation par mail ou par tél</p>
      </div>
     
      <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center',margin:'5px',height:"100%", }}>



        <img src="./images/load.png" alt="mage" />
        <h1 style={{ textAlign: 'center',color:"#E04C55" ,fontSize:"2rem",width:"300px" }}>Je recharge</h1>
        <p  style={{ textAlign: 'center', fontWeight:"bolder", color:"gray",fontSize:"1.5rem" }}> ma carte</p>
      </div>
     
      <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center',margin:'5px',height:"100%", }}>



        <img src="./images/terminal.png" alt="mage" />
        <h1 style={{ textAlign: 'center',color:"#E04C55" ,fontSize:"2rem" }}>J'utilise</h1>
        <p  style={{ textAlign: 'center', fontWeight:"bolder", color:"gray",fontSize:"1.5rem" }}>Ma carte pour payer mes achats en France et dans le monde</p>
      </div>
      
    </Box>
  );
}

export default CenteredContent;






