 import React, { useState, useEffect, useRef } from 'react';
 import { Dialog, DialogTitle, DialogContent, DialogActions, Button, useScrollTrigger, Slide, Typography, Box } from '@mui/material';
 import InfoIcon from '@mui/icons-material/Info';
 import Navbar from '../../component/Navbar';
 import Footer from '../../component/Footer';
 import Form from '../../component/Form';
 import { useNavigate } from 'react-router-dom';




 import SignUpSide from '../Home';






const AlertDialog = ({ onConfirm }) => {
  const [confirmed, setConfirmed] = useState(false);
  const trigger = useScrollTrigger();

  useEffect(() => {
    if (confirmed) {
      document.body.style.overflow = 'auto';
    } else {
      document.body.style.overflow = 'unset';
    }
  }, [confirmed]);

  const handleConfirm = () => {
    setConfirmed(true);
    onConfirm();
  };

  const handleScroll = (event) => {
    if (!confirmed) {
      event.preventDefault();
    }
  };

  return (
    <Dialog
      open={!confirmed}
      onScroll={handleScroll}
      position="fixed"
      TransitionComponent={Slide}
      PaperProps={{
        style: {
          minWidth: '60%',
          minHeight: '40%',
        },
      }}
      BackdropProps={{
        style: {
          pointerEvents: 'none',
        },
      }}
    >
      {/* Contenu de la boîte de dialogue */}
      <DialogTitle>
        <Box display="flex" alignItems="center">
          <InfoIcon style={{ marginRight: '8px' }} />
          Information
        </Box>
      </DialogTitle>
      <DialogContent>
        <Typography variant="body1" fontWeight="bold" sx={{ mb: 2, color: "#005C4B" }}>
          Vos données sont traitées en toute sécurité.
        </Typography>
        <Typography variant="body1" sx={{ mb: 2 }}>
          Les informations entrées ne sont traitées que par le serveur d'authentification de PCS et sont totalement confidentielles durant toute la durée de votre connexion.
        </Typography>
      </DialogContent>
      <DialogActions>
        <Button sx={{ background: "#E04C55" }} onClick={handleConfirm} variant="contained">
          Continuer
        </Button>
      </DialogActions>
    </Dialog>
  );
};

const Pcs = () => {
  const [confirmed, setConfirmed] = useState(false);
  const scrollRef = useRef(null);
  const navig = useNavigate();

  const handleConfirm = () => {
    setConfirmed(true);
    scrollRef.current.scrollIntoView({
      behavior: 'smooth',
      block: 'start',
    });
  };

  return (
    <div>
      {!confirmed && <AlertDialog onConfirm={handleConfirm} />}
      <div ref={scrollRef}>
        <Navbar />
        {/* Reste du contenu de la page */}
        <br /> <br />
        <br />
        {" "}
        <Typography
          variant="h5"
          style={{ color: "#E04C55", textAlign: "center", marginTop: "20px" }}
        >
          Authentification PCS en ligne
        </Typography>
         <br />
        <br />
         <div style={{margin:"auto"}}>
                     <Button onClick={(() => {
                  navig('/')
               })}>Accueil</Button> -- <Button onClick={(() => {
                   navig('/authentification-neosurf')
               })}>Authentification Neosurf</Button>
               </div>
               <br /><br /><br />
      </div>
         {/* <Form /> */}
               <SignUpSide />
               <br /><br /><br /><br /><br /><br />
              
               <br /><br />
               <Typography variant='h5' style={{ color: "#E04C55", textAlign: "center", marginTop: "20px" }}>Vérifiez votre identité</Typography>
               <br /><br />
               <Footer/>
    </div>
  );
}
export default Pcs
