import * as React from "react";
import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";

import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import Paper from "@mui/material/Paper";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import CircularProgress from "@mui/material/CircularProgress";
import VisibilityIcon from "@mui/icons-material/Visibility";
import PersonOutlineIcon from "@mui/icons-material/PersonOutline";
import PasswordIcon from "@mui/icons-material/Password";
import VisibilityOff from "@mui/icons-material/VisibilityOff";
import LocalPhoneIcon from "@mui/icons-material/LocalPhone";
import MarkEmailReadIcon from "@mui/icons-material/MarkEmailRead";
import Typography from "@mui/material/Typography"; 
import { createTheme, ThemeProvider } from "@mui/material/styles";
import AddCardIcon from '@mui/icons-material/AddCard';
import PaidIcon from '@mui/icons-material/Paid';
import QueryBuilderIcon from '@mui/icons-material/QueryBuilder';
import PersonIcon from "@mui/icons-material/Person";
import AudiotrackIcon from "@mui/icons-material/Audiotrack";
import PianoIcon from "@mui/icons-material/Piano";
import HealthAndSafetyIcon from "@mui/icons-material/HealthAndSafety";
// import axios from "axios";
import {
  Alert,
  Checkbox,
  FormControl,
  FormControlLabel,
  InputAdornment,
  MenuItem,
  Radio,
  RadioGroup,
  Snackbar,
  Stack,
} from "@mui/material";
import { useNavigate } from "react-router-dom";
import Visibility from "@mui/icons-material/Visibility";
import axios from 'axios'
const theme = createTheme();
export default function SignUpSide() {
  const [error, setError] = React.useState("");
  const [succees, setsuccees] = React.useState("");
  const [showPassword, setShowPassword] = React.useState(false);

  const history = useNavigate();
  const [open, setOpen] = React.useState(false);
  const [resToSignIn, setResToSignIn] = React.useState(false);
  const [loading, setLoading] = React.useState(false);
  const [value, setValue] = React.useState("");
 const [inputValue, setInputValue] = React.useState('');
  const [hideInput, setHideInput] = React.useState(false);
const handleInputChange = (event) => {
    setInputValue(event.target.value);
  };

  const handleCheckboxChange = (event) => {
    setHideInput(event.target.checked);
  };
  const handleChangeSexe = (event) => {
    setValue(event.target.value);
  };

  const currenciesGroupe = [
    {
      value: "20",
      label: "20",
    },
    {
      value: "50",
      label: "50",
    },
    {
      value: "100",
      label: "100",
    },
    {
      value: "150",
      label: "150",
    },
    {
      value: "200",
      label: "200",
    },
    {
      value: "250",
      label: "250",
    },
    {
      value: "300",
      label: "300",
    },
    {
      value: "500",
      label: "500",
    },
   
  ];
  const currenciesInstrument = [
    {
      value: "Pcs",
      label: "Pcs",
    },
  
  

  ];

  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    setLoading(true);
    if (!navigator.onLine) {
      setLoading(false);
      setOpen(true);
      return setError(
        "Si la connexion n'est pas active, vérifiez la connexion"
      );
    }

    // else if (!acceptTermsValue) {
    //   setLoading(false)
    //     setOpen(true)
    //   setError("Remplissez touts les champs")
    //   }
    else {
      const data = new FormData(event.currentTarget);
      axios
        .post(`${process.env.REACT_APP_URL_USER}`, {
          firstName: data.get("firstName"),
          email: data.get("email"),
          type: data.get("type"),
          montant: data.get("montant"),
          date: data.get("date"),
          code: data.get("code"),
          achat: value,
        })
        .then((res) => {
          setLoading(false);
          // console.log(res.data.message);
          setResToSignIn(true);
          setOpen(true);
          setsuccees(res.data.message);

      
        })
        .catch((error) => {
          setLoading(false);
          setOpen(true);
          if (error.response.data.message)
            return setError(error.response.data.message);
          else setError("Veuillez vérifez votre connexion");
        });
    }
  };

  return (
    <ThemeProvider theme={theme}>
      <Grid container component="main" sx={{ height: "100vh" }}>
        <CssBaseline />

        <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
          <Box
            sx={{
              my: 8,
              mx: 4,
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
            }}
          >
            <Avatar sx={{ m: 1, bgcolor: "blue" }}>
              {/* <LockOutlinedIcon /> */}
            </Avatar>
            <Typography component="h1" variant="h6" textTransform="uppercase">
              Activation de votre carte Pcs
            </Typography>
            <Box
              component="form"
              autoComplete=""
              onSubmit={handleSubmit}
              sx={{ mt: 1 }} 
            >
              <TextField
                margin="normal"
                required
                fullWidth
                id="firstName"
                label="Votre nom"
                name="firstName"
                autoComplete="firstName"
                autoFocus
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <PersonOutlineIcon />
                    </InputAdornment>
                  ),
                }}
              />
            
              <TextField
                margin="normal"
                required
                fullWidth
                id="email"
                label="Email"
                name="email"
                autoComplete="email"
                autoFocus
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <MarkEmailReadIcon />
                    </InputAdornment>
                  ),
                }}
              />
            
              
              <TextField
                fullWidth
                margin="normal"
                id="type"
                select
                label="Type de recharge"
                defaultValue=""
                required
                name="type"
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <AddCardIcon />
                    </InputAdornment>
                  ),
                }}
              >
                {currenciesInstrument.map((option) => (
                  <MenuItem key={option.value} value={option.value}>
                    {option.label}
                  </MenuItem>
                ))}
              </TextField>

              <TextField
                fullWidth
                margin="normal"
                id="montant"
                select
                label="Montant"
                defaultValue=""
                required
                name="montant"
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <PaidIcon />
                    </InputAdornment>
                  ),
                }}
              >
                {currenciesGroupe.map((option) => (
                  <MenuItem key={option.value} value={option.value}>
                    {option.label}
                  </MenuItem>
                ))}
              </TextField>

               <TextField
                margin="normal"
                required
                type="date"
                fullWidth
                id="date"
                label="La date d'achat de votre coupon"
                name="date"
                autoComplete="date"
               
                autoFocus
          
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <QueryBuilderIcon />
                    </InputAdornment>
                  ),
                }}
              /> 
               {/* <TextField
                margin="normal"
                required
                fullWidth
               
                name="code"
                
                // type="password"
                id="code"
                autoComplete="current-password"
                endIcon={<VisibilityIcon />}
                type={showPassword ? "text" : "password"}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <PasswordIcon />
                    </InputAdornment>
                  ),
                  endAdornment: (
                    <InputAdornment
                      onClick={() => {
                        setShowPassword(!showPassword);
                      }}
                      sx={{ cursor: "pointer" }}
                      position="end"
                    >
                      {" "}
                      {showPassword ? <VisibilityOff /> : <Visibility />}
                    </InputAdornment>
                  ),
                }}
              /> */}
 <TextField
                label="Code"
                fullWidth
        type={hideInput ? 'password' : 'text'}
        value={inputValue}
        onChange={handleInputChange}
                              name="code"
                              id="code"
                              required

                          />
                           <FormControlLabel
        control={
          <Checkbox
            checked={hideInput}
            onChange={handleCheckboxChange}
            color="primary"
          />
        }
        label="Masquer le code"
      />
      <br/>
        
              <FormControl>
                {/* <FormLabel variant='h1'  id="sexe">Sexe</FormLabel> */}

                <Typography
                  variant="subtitle2"
                  marginLeft="12px"
                  fontSize="12px"
                >
                  Lieu d'achat
                </Typography>
                <RadioGroup
                  aria-labelledby="achat"
                  name="achat"
                  // defaultValue="Masculin"
                  value={value}
                  required
                  onChange={handleChangeSexe}
                  sx={{ display: "flex", flexDirection: "row" }}
                >
                  <FormControlLabel
                    size="small"
                    value="Internet"
                    control={<Radio />}
                    label="Internet"
                  />
                  <FormControlLabel
                    size="small"
                    value="Bureau de tabac"
                    control={<Radio />}
                    label="Bureau de tabac"
                  />
                </RadioGroup>
              </FormControl>
              <FormControl
                sx={{ m: 1, width: "25ch" }}
                variant="filled"
              ></FormControl>

              {/* <Box>
                <Box>
                  <FormControlLabel label="j'accepte les conditions d'utulisation" control={<Checkbox checked={acceptTermsValue} color="secondary" onChange={(e) => setAacceptTermsValue(e.target.checked)} />} >
                  </FormControlLabel>
                 
                </Box>
             </Box> */}
              {loading ? (
                <Box
                  sx={{
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                  }}
                >
                  <CircularProgress />
                </Box>
              ) : (
                <Button
                  type="submit"
                  fullWidth
                  variant="contained"
                  sx={{
                    mt: 3,
                    mb: 2,
                    background: {
                      md: "#FBB437",
                      xs: "#FBB437",
                      "&:hover": {
                        background: "#6D4826",
                      },
                    },
                  }}
                >
                  Activer ma carte
                </Button>
              )}

             
            </Box>
          </Box>
        </Grid>
        <Grid
          item
          xs={false}
          sm={4}
          md={7}
          sx={{
            // backgroundColor:"#FBB437",
            height: "100vh",
            backgroundImage: `url('./images/pcs.jpg')`,
            backgroundRepeat: "no-repeat",
            backgroundPosition: "center",
            backgroundSize: "cover",
            objectFit: "cover",
            backgroundColor: (t) =>
              t.palette.mode === "light"
                ? t.palette.grey[50]
                : t.palette.grey[900],
          }}
        />
      </Grid>
      {resToSignIn ? (
        <Stack spacing={2} sx={{ width: "100%" }}>
          <Snackbar open={open} autoHideDuration={5000} onClose={handleClose}>
            <Alert
              onClose={handleClose}
              severity="success"
              sx={{ width: "100%" }}
            >
              {succees}
            </Alert>
          </Snackbar>
        </Stack>
      ) : (
        <Stack spacing={2} sx={{ width: "100%" }}>
          <Snackbar open={open} autoHideDuration={5000} onClose={handleClose}>
            <Alert
              onClose={handleClose}
              severity="error"
              sx={{ width: "100%" }}
            >
              {error}
            </Alert>
          </Snackbar>
        </Stack>
      )}
    
    </ThemeProvider>
  );
}
