import React from 'react';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import { styled } from '@mui/material/styles';
import { Link } from 'react-router-dom';
import TextLoop from 'react-text-loop';
import { Box, Button } from '@mui/material';

const HeaderContainer = styled(AppBar)`
  background: whitesmoke;
`;

const Header = () => {
  return (
    <HeaderContainer position="static">
      <Toolbar>
        <div
          style={{
            width: '100%',
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            height: 'auto',
            color: '#E04C55',
          }}
        >
          <Typography variant="body1" sx={{ mb: 4 }}>
            Authentifier ma récharge en 2 minutes
          </Typography>
          <Typography variant="h4" sx={{ mb: 1, fontWeight: 'bolder' }}>
            Simple pour authentifier vos recharges
          </Typography>
          <TextLoop interval={3000}>
            <Typography variant="body1" sx={{ mb: 4 }}>
              <Link to="/authentification-pcs" style={{ textDecoration: 'none', color: 'inherit',textTransform:'uppercase' }}>
            Authentification-Pcs
              </Link>
            </Typography>
            <Typography variant="body1" sx={{ color: '#CF7131', mb: 4 }}>
              <Link to="/authentification-neosurf" style={{ textDecoration: 'none', color: 'inherit',textTransform:'uppercase' }}>
           Authentification-Neosurf
              </Link>
            </Typography>
          </TextLoop>
         <Typography sx={{ color: 'gray' }} variant="body1">
            Votre recharge, votre Authentification en main. Votre garantie. Authentifier votre recharge en toute sécurité
          </Typography>
          <Box sx={{ mb: 4 }}>
            <Button
              component={Link}
              to="/authentification-pcs"
              sx={{ background: '#E04C55', margin: '5px' }}
              variant="contained"
            >
             Authentification PCS
            </Button>
            <Button
              component={Link}
              to="/authentification-neosurf"
              sx={{ margin: '5px',textTransform:"uppercase", background: '#CF7131' }}
              variant="contained"
            >
             Authentification NEOSURF
            </Button>
          </Box>
        </div>
      </Toolbar>
    </HeaderContainer>
  );
};

export default Header;
