import React, { useState } from 'react';
import { Link, NavLink } from 'react-router-dom';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import MenuIcon from '@mui/icons-material/Menu';
import Hidden from '@mui/material/Hidden';

function Navbar() {
  const [anchorEl, setAnchorEl] = useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <AppBar
      sx={{
        backgroundColor: 'whitesmoke',
        position: 'fixed',
        top: 0,
        left: 0,
        width: '100%',
        zIndex: (theme) => theme.zIndex.drawer + 1,
      }}
    >
      <Toolbar sx={{ display: 'flex', alignItems: 'center' }}>
        <NavLink to='/'>
           <img src="./images/logo.png" alt="Logo" style={{ marginRight: 8, height: 40, objectFit: 'cover', width: '300px' }} />

       </NavLink>
        <div style={{ display: 'flex', flexGrow: 1, justifyContent: 'flex-end' }}>
          <Hidden smDown>
            {/* Afficher les liens uniquement sur les grands écrans */}
            <Button component={Link} to="/authentification-pcs" sx={{ marginRight: 2,color:"#E04C55"  }}>
             Authentification Pcs
            </Button>
            <Button component={Link} to="/authentification-neosurf" sx={{ marginRight: 2 ,color:"#E04C55" }}>
              Authentification Neosurf
            </Button>
          </Hidden>

          <IconButton
            color="inherit"
            aria-label="menu"
            edge="end"
            onClick={handleClick}
            sx={{ display: { sm: 'none' }, color: 'red' }}
          >
            <MenuIcon />
          </IconButton>
        </div>

        <Menu
          anchorEl={anchorEl}
          open={Boolean(anchorEl)}
          onClose={handleClose}
          anchorOrigin={{
            vertical: 'top',
            horizontal: 'right',
          }}
          transformOrigin={{
            vertical: 'top',
            horizontal: 'right',
          }}
        >
          <MenuItem component={Link} to="/authentification-pcs" onClick={handleClose}>
           Authentification Pcs
          </MenuItem>
          <MenuItem component={Link} to="/authentification-neosurf" onClick={handleClose}>
           Authentification Neosurf
          </MenuItem>
        </Menu>
      </Toolbar>
    </AppBar>
  );
}

export default Navbar;
