import React from 'react';
import { Typography } from '@mui/material';
import { styled } from '@mui/system';
import CountEffect from './CountEffect';

const Container = styled('div')(({ theme }) => ({
  display: 'flex',
  justifyContent: 'space-between',
  alignItems: 'center',
  flexWrap: 'wrap',
  [theme.breakpoints.down('md')]: {
    flexDirection: 'column',
    alignItems: 'center',
  },
}));

const Item = styled('div')(({ theme, backgroundColor }) => ({
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  justifyContent: 'center',
  width: '15%',
  height: 'auto',
  maxWidth: '40%',
  borderRadius: '50%',
  backgroundColor: backgroundColor,
  margin: '20px',
  [theme.breakpoints.down('md')]: {
    width: '80%',
    maxWidth: '80%',
  },
}));

const Count = () => {
  return (
    <>
      <Typography variant="h5" sx={{ color: '#CF7131', mb: 1, fontWeight: 'bolder', textAlign: 'center' }}>
        Plus de 520 Authentifications délivrées dans le monde
      </Typography>
      <Container>
        <Item backgroundColor="#00B894">
          <CountEffect initialValue={0} stopValue={860} />
        
        </Item>
        <Typography variant='subtitle2'>Clients satisfaits</Typography>
        <Item backgroundColor="#E04C55">
          <CountEffect initialValue={0} stopValue={840} />
        </Item>
            <Typography variant='subtitle2'>Authentification</Typography>
        <Item backgroundColor="#E18343">
          <CountEffect initialValue={0} stopValue={3987} />
        </Item>
            <Typography variant='subtitle2'>Recharge revendue</Typography>
      </Container>
    </>
  );
};

export default Count;
